import React from 'react';
import {NavLink} from 'react-router-dom';

import './../../../assets/scss/style.scss';
import Aux from "../../../hoc/_Aux";
import Breadcrumb from "../../../App/layout/AdminLayout/Breadcrumb";
import { AuthConsumer } from '../../../App/contexts/AuthContext';
import { Redirect } from 'react-router-dom';
import config from '../../../config';

const { baseURL } = config;

function SignInWrapper() {
    return (
        <AuthConsumer>
            {(context) => {
                const { authState, addUserToAuthContext } = context;

                if (authState.loading) {
                    return (
                        <Aux>
                            <Breadcrumb />
                        </Aux>
                    );
                } else if (authState.isAuthenticated) {
                    return (
                        <Redirect to={'/profile'} />
                    );
                } else {
                    return (
                        <SignIn addUserToAuthContext={addUserToAuthContext} />
                    );
                }
            }}
        </AuthConsumer>
    );
}

class SignIn extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            errors: [],
        };

        this.signIn = this.signIn.bind(this);
    }

    signIn(e) {
        const email = document.getElementById('sign-in-email').value;
        const password = document.getElementById('sign-in-password').value;

        const signin_payload = {
            username: email,
            email: email,
            password: password,
        };

        fetch(baseURL + '/api/auth/login', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(signin_payload),
        })
            .then(response => {
                if (response.status !== 200) {
                    throw Error("Invalid Credentials");
                } else {
                    return response.json();
                }
            })
            .then(data => {
                this.props.addUserToAuthContext(data.user, data.token);
            })
            .catch((error) => {
                console.log(error);
                this.setState({ errors: ["Invalid Credentials"]});
            });
    }

    render () {
        return(
            <Aux>
                <Breadcrumb/>
                <div className="auth-wrapper">
                    <div className="auth-content">
                        <div className="auth-bg">
                            <span className="r"/>
                            <span className="r s"/>
                            <span className="r s"/>
                            <span className="r"/>
                        </div>
                        <div className="card">
                            <div className="card-body text-center">
                                <div className="mb-4">
                                    <i className="feather icon-unlock auth-icon"/>
                                </div>
                                <h3 className="mb-4">Login</h3>
                                <div className="input-group mb-3">
                                    <input id="sign-in-email" type="email" className="form-control" placeholder="Email"/>
                                </div>
                                <div className="input-group mb-4">
                                    <input id="sign-in-password" type="password" className="form-control" placeholder="password"/>
                                </div>
                                <div className="form-group text-left">
                                    <div className="checkbox checkbox-fill d-inline">
                                        <input type="checkbox" name="checkbox-fill-1" id="checkbox-fill-a1" />
                                            <label htmlFor="checkbox-fill-a1" className="cr"> Save credentials</label>
                                    </div>
                                </div>
                                <button className="btn btn-primary shadow-2 mb-4" onClick={this.signIn}>Login</button>
                                <p className="mb-2 text-muted">Forgot password? <NavLink to="/auth/reset-password-1">Reset</NavLink></p>
                                <p className="mb-0 text-muted">Don’t have an account? <NavLink to="/auth/signup-1">Signup</NavLink></p>
                            </div>
                        </div>
                    </div>
                </div>
            </Aux>
        );
    }
}

export default SignInWrapper;